.footer-cart {
  padding: 450px 0px 50px 0px;
  background-image: url('../img/svg/head_goat.svg');
  background-repeat: no-repeat;
  background-position: center;

  &__adress {
    padding-top: 30px;
    display: flex;
    justify-content: space-around;
    margin-bottom: 28px;
  }



  &__sotial {
    display: flex;
    justify-content: center;
    margin-bottom: 28px;
  }

  &__sotial-item {
    &:not(:last-child) {
      margin-right: 60px;
    }
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
  }

  &__descr {
    margin: 0;
    font-family: 'Rockwell Nova';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 26px;

    span {
      display: block;
    }
  }

  &__tel {
    font-weight: 700;
    font-size: 20px;
    line-height: 26px;
    text-align: center;
    text-transform: uppercase;
    color: var(--light-color);
  }

  &__year {
    padding-top: 30px;
    padding-right: 90px;
  }

  &__store {
    margin: 54px 0px 84px 0px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  }

